/**
 * Site footer
 */
.site-footer {
    border-top: 1px solid $cmu-gray;
    padding: 0;
    background-color: transparent;
    margin-bottom: 0;
}

/**
 * Page content
 */
.page-content {
    padding: $spacing-unit 0;
}

/**
 * Posts
 */
.post-header {
    margin-bottom: $spacing-unit;
}

h1 {
    letter-spacing: 0px;
    line-height: 1;
    font-family: 'Titillium Web', sans-serif;
    color: #C41230;
    margin-bottom: -10px;
}

.post-content {
    margin-bottom: $spacing-unit;

    h2 {
        font-family: 'Titillium Web', sans-serif;
        font-weight: 500;
    }

    h3 {
        font-style: bold;
        color: #C41230;
        font-size: 1.1em;
    }

    h4 {
        font-size: 16px;
        margin-top: -15px;
    }

    h5 {
        font-size: 16px;
        font-style: normal;
        margin-top: -15px;
        color: #C41230;
    }

    h6 {
        font-size: 16px;
        font-style: normal;
        margin-top: -15px;
        margin-bottom: 35px;
        color: $brand-color;
    }
}

.footnotes ol li {
    list-style-type: decimal;
}

.footnotes ol {
    font-size: .85em;
    color: #666666;
}

html {
    overflow-y: scroll;
}

