/**
 * Index page specific styles
 */

.profile-container {
    margin-bottom: 30px;
    margin-left: 30px;
    margin-right: 30px;
    display: flex;
    flex-direction: row;
    gap: 30px;
    align-items: stretch;
}

.profile-image-wrapper {
    flex-shrink: 0;
    display: flex;
    box-sizing: border-box;
}

.profile-image {
    width: 230px;
    height: 100%;
    object-fit: cover;
    display: block;
    border: 1px solid #000;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.contact-box {
    padding: 0;
    background-color: #f9f9f9;
    line-height: 1.3;
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    border: 1px solid #000;
    box-sizing: border-box;
}

.contact-info {
    flex: 1;
    padding: 0 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-word;
    min-width: 0;
}

.contact-links {
    padding: 20px 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow-wrap: break-word;
    word-wrap: break-word;
    min-width: 0;
}

.links-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.link-item {
    display: flex;
    align-items: center;
    gap: 8px;
    text-decoration: none;
    color: inherit;
    transition: color 0.2s ease;
    font-size: 1em;
    font-weight: 600;
}

.link-item:hover {
    color: var(--cmu-red);
}

.link-icon-img {
    width: 20px;
    height: 20px;
    object-fit: contain;
}

.link-icon-img[src*="strava"],
.link-icon-img[src*="pdf_icon_cv"] {
    border-radius: 2px;
}

.contact-box p {
    margin: 0;
    line-height: 1.3;
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-word;
}

.contact-box a {
    overflow-wrap: break-word;
    word-wrap: break-word;
    word-break: break-word;
}

.contact-box .title {
    color: $cmu-red;
    font-size: 1.1em;
}

.contact-box .section-break {
    margin-bottom: 10px;
}

.office-info {
    white-space: nowrap;
}

/* Office text wrapping at 350px and below */
@media screen and (max-width: 350px) {
    .office-info {
        white-space: normal;
        overflow-wrap: break-word;
        word-wrap: break-word;
        word-break: break-word;
    }
}

/* Tablet responsive styles (iPad) */
@media screen and (min-width: 601px) and (max-width: 1024px) {
    .profile-container {
        margin-left: 15px;
        margin-right: 15px;
        flex-direction: column;
    }

    .contact-box {
        flex-direction: column;
    }

    .profile-image-wrapper {
        width: 100%;
        text-align: center;
        margin-bottom: 15px;
    }

    .profile-image {
        width: 100%;
        max-width: 300px;
        height: auto;
        margin: 0 auto;
    }

    .contact-info {
        width: 100%;
        padding: 20px 30px;
    }

    .contact-links {
        padding: 20px 15px;
        min-width: auto;
    }
}

/* Mobile responsive styles */
@media screen and (max-width: 600px) {
    .profile-container {
        margin-left: 10px;
        margin-right: 10px;
        flex-direction: column;
    }

    .contact-box {
        flex-direction: column;
        padding: 0;
        font-size: 0.9em;
    }

    .profile-image-wrapper {
        width: 100%;
        text-align: center;
        margin-bottom: 15px;
    }

    .profile-image {
        width: 98%;
        max-width: 250px;
        height: auto;
        margin: 0 auto;
        border: 1px solid #000;
    }

    .contact-info {
        width: 100%;
        padding: 15px;
        max-width: 100%;
        box-sizing: border-box;
    }

    .contact-links {
        padding: 15px;
        min-width: auto;
    }

    .contact-box .title {
        font-size: 1em;
    }

    .office-info {
        white-space: normal;
        overflow-wrap: break-word;
        word-wrap: break-word;
        word-break: break-word;
    }
}
