/**
 * Mobile responsive styles for all pages
 */

@media screen and (max-width: 768px) {
    /* Reduce body font size slightly on mobile */
    body {
        font-size: 1em;
        text-align: left;
    }

    /* Adjust wrapper padding for mobile */
    .wrapper {
        padding-right: 15px;
        padding-left: 15px;
    }

    /* Adjust margins for content sections */
    .home {
        margin-right: 15px !important;
        margin-left: 15px !important;
    }

    /* Adjust all content divs with 30px margins */
    div[style*="margin-right: 30px"] {
        margin-right: 15px !important;
        margin-left: 15px !important;
        font-size: 0.95em !important;
    }

    /* Make headings smaller on mobile */
    h1[style*="font-size: 1.3em"] {
        font-size: 1.2em !important;
    }

    /* Adjust research project images */
    div[style*="width: 200px"] {
        width: 100% !important;
        margin-right: 0 !important;
        margin-bottom: 15px !important;
    }

    /* Make flex items stack vertically */
    div[style*="display: flex"][style*="margin-bottom: 2em"] {
        flex-direction: column !important;
    }

    /* Adjust publication/student list items */
    div[style*="display: flex"][style*="margin-bottom: 1em"],
    div[style*="display: flex"][style*="margin-bottom: 0.2em"] {
        flex-direction: row !important;
        align-items: flex-start !important;
    }

    /* Make images in publications responsive */
    img[style*="width: 200px"] {
        width: 100%;
        max-width: 200px;
    }

    /* Adjust icon sizes */
    img[width="20"],
    img[width="26"] {
        width: 18px !important;
        height: auto !important;
    }

    /* Make tables responsive */
    table {
        display: block;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
    }

    /* Adjust footer for mobile */
    .site-footer {
        background-size: auto 150px !important;
    }
}

/* Extra small devices (phones in portrait mode) */
@media screen and (max-width: 480px) {
    body {
        font-size: 0.95em;
    }

    .wrapper {
        padding-right: 10px;
        padding-left: 10px;
    }

    .home,
    div[style*="margin-right: 30px"] {
        margin-right: 10px !important;
        margin-left: 10px !important;
    }

    /* Further reduce heading sizes */
    h1[style*="font-size: 1.3em"] {
        font-size: 1.1em !important;
    }

    /* Reduce bullet spacing */
    span[style*="margin-right: 15px"] {
        margin-right: 10px !important;
    }
}
